<template>
    <div class="container">
        <img id="logo" src="../assets/logo.png" />
        <div class="main-section">
            <div class="body-section">
              <form class="login-form">
                <h1>منظومة الهيئة العامة للطرق والكباري</h1>
                  <div class="textb">
                      <input type="text" id="email" v-model="email" placeholder="الحساب الالكتروني">
                  </div>
                  <div class="textb">
                      <input type="password" id="password" v-model="password" placeholder="كلمة السر">
                  </div>
                  <div class="form-input">
                      <button v-on:click="login" type="submit" class="button fas fa-arrow-right"></button>
                  </div>
              </form>
          </div>
        </div>
    </div>
</template>

<script>
import db from "firebase/app";
export default {
    name: 'login',
    data: function() {
        return {
            email: '',
            password: ''
        }
    },
    methods: {
        login: function(e) {
            db.auth().signInWithEmailAndPassword(this.email, this.password)
                .then(user => {
                    alert('You are logged in.' )
                    this.$router.go({path: this.$router.path})
                },
                err => {
                    alert(err.message)
                })
            e.preventDefault()
        }
    }
}
</script>

<style>
#address {
    padding-top: 5%;
}

body{
  background: url('../assets/background.jpg') no-repeat center;
  background-size: cover;
  min-height: 100vh;
}

#logo{ 
    
	position:fixed; 
    width:88px;
    height: auto;
	top:0; 
	left:0; 
}

.login-form{
  width: calc(100% - 20px);
  max-width: 600px;
  background-color: #fff;
  padding: 50px 40px;
}

.login-form h1{
  text-align: center;
    padding-bottom: 10px;
    padding-top: 10px;
  margin: 46px 0;
  font-size: 30px;
 /* color: #111;*/
  color :aliceblue;
  background-color:firebrick;
}

.textb{
  margin-bottom: 20px;
  position: relative;
}

.textb input{
  width: 100%;
  height: 70px;
  border: none;
  background-color: #ededed;
  border-radius: 4px;
  color: #333;
  font-size: 18px;
  font-weight: 700;
  padding: 14px 60px 0 10px;
}

.placeholder{
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  position: absolute;
  line-height: 70px;
  top: 0;
  left: 20px;
  color: #9d9d9d;
  user-select: none;
  pointer-events: none;
}

.show-password{
  position: absolute;
  right: 20px;
  line-height: 70px;
  color: #9d9d9d;
  font-size: 22px;
  cursor: pointer;
}

.textb input:focus ~ .placeholder,
.textb input:valid ~ .placeholder{
  left: 10px;
  top: -16px;
}

.checkbox{
  display: flex;
  align-items: center;
  position: relative;
  font-size: 18px;
  font-weight: 500;
  color: #333;
  margin: 20px 0;
}

.checkbox input{
  width: 24px;
  height: 24px;
  -webkit-appearance: none;
  background-color: #ededed;
  border-radius: 4px;
  margin-right: 10px;
  cursor: pointer;
}

.checkbox .fa-check{
  position: absolute;
  color: #fff;
  font-size: 14px;
  width: 24px;
  text-align: center;
  user-select: none;
  pointer-events: none;
  opacity: 0;
}

.checkbox input:checked{
  background-color: #bc252a;
}

.checkbox input:checked + .fa-check{
  opacity: 1;
}

.button{
  display: block;
  width: 100px;
  height: 100px;
  margin: 40px auto;
  background-color: #bc252a;
  color: #fff;
  font-size: 40px;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: .3s linear;
}

.button:hover{
  opacity: .85;
}

.login-form a{
  display: block;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  color: #555;
  text-transform: uppercase;
  font-weight: 500;
  margin-bottom: 8px;
  transition: .2s linear;
}

.login-form a:hover{
  color: #111;
}

.button:disabled{
  background-color: transparent;
  color: #ddd;
  border: 2px solid;
}

</style>


