<template>
    <div class="container">
        <img id="logo" src="../assets/logo.png" />
        <div class="main-section">
            <div class="body-section">
                <form class="login-form">
                    <h1>منظومة الهيئة العامة للطرق والكباري</h1>
                    <div class="textb">
                        <input type="text" id="email" v-model="email" placeholder="الحساب الالكتروني">
                    </div>
                    <div class="textb">
                        <input type="password" id="password" v-model="password" placeholder="كلمة السر">
                    </div>
                    <div class="form-input">
                        <button v-on:click="register" type="submit" class="button fas fa-arrow-right"></button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import db from "firebase/app";
export default {
    name: 'register',
    data: function() {
        return {
            email: '',
            password: ''
        }
    },
    methods: {
        register: function(e) {
            db.auth().createUserWithEmailAndPassword(this.email, this.password)
                .then(user => {
                    alert('Account created.')
                    this.$router.go({path: this.$router.path})
                },
                err => {
                    alert(err.message)
                })
            e.preventDefault()
        }
    }
}
</script>

<style>
#logo{  
	position:fixed; 
  width:88px;
  height: auto;
	top:0; 
	left:0; 
}

.button{
  display: block;
  width: 100px;
  height: 100px;
  margin: 40px auto;
  background-color: #bc252a;
  color: #fff;
  font-size: 40px;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: .3s linear;
}

.button:hover{
  opacity: .85;
}

.button:disabled{
  background-color: transparent;
  color: #ddd;
  border: 2px solid;
}

</style>


