<template>
    <nav>
        <img id="logo" src="../assets/logo.png" />
        <div id="nav" class="nav-wrapper">
            <div class="container">
                <router-link to="/" class="brand-logo right" style="padding-right: 14.3%;">هيئة الطرق والكباري</router-link>
                <ul class="left">
                    <li v-if="isLoggedIn"><button v-on:click="logout" class="btn black">تسجيل خروج</button></li>
                    <li v-if="!isLoggedIn"><router-link to="/register">تسجيل حساب جديد</router-link></li>
                    <li v-if="!isLoggedIn"><router-link to="/login">تسجيل الدخول</router-link></li>
                    <li v-if="isLoggedIn"><router-link to="/">الصفحة الرئيسية</router-link></li>
                    <li v-if="isLoggedIn"><span class="email black-text">{{currentUser}}</span></li>
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
import db from 'firebase'
export default {
    name: 'navbar',
    data() {
        return {
            isLoggedIn: false,
            currentUser: false
        }
    },
    created() {
        if(db.auth().currentUser) {
            this.isLoggedIn = true,
            this.currentUser = db.auth().currentUser.email
        }
    },
    methods: {
        logout: function() {
            db.auth().signOut().then(() => {
                this.$router.go({path: this.$router.path})
            })
        }
    }
}
</script>

<style scoped>
    #nav {
        color: firebrick;
    }
    #logo{ 
    
	position:fixed; 
    width:88px;
    height: auto;
	top:0; 
	left:0; 
}
</style>


