<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header center"><h4>متابعة المشروعات</h4></li>
            <div v-for="main_job in main_jobs" v-bind:key="main_job.main_job_id">
                <li class="collection-item">
                    <div class="chip">{{main_job.main_job_id}}: {{main_job.name}}</div>
                    <router-link class="secondary-content"
                        v-bind:to="{name: 'view-main-job', params: {main_job_id: main_job.main_job_id}}">
                        تفاصيل
                    </router-link>
                    <button @click="deleteMainJob(main_job.main_job_id, main_job.name)" class="red btn">محو المجموعة</button>
                </li>
            </div>
        </ul>

        <div class="fixed-action-btn">
            <router-link :to="{name: 'new-main-job'}" class="btn-floating btn-large red">
                اضافة
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data() {
        return {
            main_jobs: []
        }
    },
    mounted() {
        db.collection('main_jobs').get().then(querySnapshot =>  {
            querySnapshot.forEach(doc => {
                const data = {
                    'main_job_id': doc.data().main_job_id,
                    'name': doc.data().name,
                }
                this.main_jobs.push(data)
            });
        })
    },
    methods: {
        deleteMainJob(id, jobName) {
            if (confirm(`Are you sure you want to delete main project with name ${jobName}`)) {
                db.collection('main_jobs').where('main_job_id', '==', id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                    })
                })
                db.collection('projects').where('main_job_id', '==', id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                    })
                })
                db.collection('sections').where('main_job_id', '==', id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                    })
                })
                db.collection('companies').where('main_job_id', '==', id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                })
                
            }
        }
    }
}
</script>


